.container {
    background-color: var(--text-color);
    min-height: 140vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.wrapper{
    width: 100%;
    height: 100%;
    max-width: 1440px;
}
.header{

}
.footer {
    /*height: 90vh;*/
    /*background-color: #111F28;*/
    /*clip-path: polygon(51% 21%, 100% 0, 100% 99%, 0 99%, 0 0);*/
    /*display: flex;*/
    /*justify-content: center;*/
    /*align-items: center;*/
    background-color: #111F28;
    clip-path: polygon(51% 21%, 100% 0, 100% 99%, 0 99%, 0 0);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.title {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 50px 0;
}

.title h3 {
    color: var(--main-color);
    font-weight: 700;
}

.aboutContainer {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: start;
}

.left {
    width: 40%;
    display: flex;
    flex-direction: column;
    align-items: start;
}
.leftTop h3, .leftTop p {
    margin: 10px 0;
}
.leftBottom h3,.leftBottom span,.leftBottom p {
    margin: 10px 0;
}

.right {
    width: 40%;
    display: flex;
    flex-direction: column;
    align-items: start;
}
.right ul li,.right h3, .right span, .right p  {
    margin: 10px 0;
}
.imageContainer{
    width: 100%;
    height: 34vh;
    display: flex;
    justify-content: start;
    margin-bottom: 20px;
}
.right img {
width: 50%;
min-width: 140px;
height: 100%;
object-fit: cover;
border-radius: 15px;
padding: 5px;
}

.footerContainer{
display: flex;
flex-direction: column;
align-items: center;
margin-top: 100px;
margin-left: 40px;
}
.footer h3{
font-size: 40px;
font-weight: 700;
color: var(--text-color);
}
.footer span{
font-size: 40px;
font-weight: 700;
color: var(--main-color);
}
.footer h1{
font-size: 80px;
font-weight: 700;
color: var(--text-color);
}
.footer h1 span {
font-size: 80px;
font-weight: 700;
color: var(--main-color);
}

.btn{
display: flex;
justify-content: center;
align-items: center;
background-color: var(--main-color);
color: var(--text-color);
font-weight: 600;
font-size: 28px;
border-radius: 10px;
height: 50px;
width: 15vw;
min-width: 150px;
padding-top: 2px;
margin-top: 20px;
cursor: pointer;
box-shadow: 0 0 1rem var(--main-color);
transition: .5s ease;
}
.btn:hover{
box-shadow: none;
}
