.container {
    background-image: url("../../../assets/image/bg-image3.bmp");
    background-size: cover;
    background-position: center;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.wrapper{
    display: flex;
    width: 100%;
    height: 100%;
    max-width: 1440px;
    justify-content: end;
    align-items: center;
}
.formContainer {
    /*width: 35%;*/
    /*height: 60%;*/
    /*max-width: 900px;*/
    /*max-height: 800px;*/
    /*backdrop-filter: blur(15px) saturate(120%);*/
    /*border-radius: 15px;*/
    /*margin-right: 100px;*/
    /*display: flex;*/
    /*flex-direction: column;*/
    /*align-items: center;*/
    /*justify-content: center;*/
    /*text-align: center;*/
    height: 60%;
    max-width: 541px;
    max-height: 548px;
    -webkit-backdrop-filter: blur(15px) saturate(120%);
    backdrop-filter: blur(15px) saturate(120%);
    border-radius: 15px;
    margin-right: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.formTitle{
    margin: 0px 0 20px 0;
    display: flex;
    flex-direction: column;
    padding: 0 15%;
}
.formTitle h4{
    margin-bottom: 10px;
    color: var(--text-color);
    font-weight: 700;
}
.formTitle span{
    color: var(--text-color);
    font-weight: 350;
}
.form{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
.form input{
    width: 80%;
    border-radius: 10px;
    padding: 15px;
    margin: 10px 0;
}
.form textarea{
    width: 80%;
    border-radius: 10px;
    padding: 15px;
    resize: none;
    margin: 10px 0;
}
.btn{
    width: 80%;
    height: 15%;
    border-radius: 10px;
    padding: 5px 0 5px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--main-color);
    color: var(--text-color);
    font-size: 28px;
    font-weight: 600;
    cursor: pointer;
    box-shadow: 0 0 1rem var(--main-color);
    transition: .5s ease;
    margin: 10px 0;
}
.btn:hover{
    box-shadow: none;
}
@media screen and (max-width: 3250px){
    .formTitle h4{
        font-size: 30px;
    }
    .formTitle span {
       font-size: 20px;
    }
}
@media screen and (max-width: 1800px){
    .formTitle h4{
        font-size: 26px;
    }
    .formTitle span {
        font-size: 18px;
    }
    .btn{
        font-size: 22px;
    }
}
@media screen and (max-width: 1200px){
    .formTitle h4{
        font-size: 22px;
    }
    .formTitle span {
        font-size: 15px;
    }
    .btn{
        font-size: 18px;
    }
    .form input, .form textarea{
        padding: 8px;
    }
}
@media screen and (max-width: 960px){
    .container{
        justify-content: center;
    }
    .formContainer{

        width: 70%;
        height: 70%;
    }
}
@media screen and (max-width: 760px){
    .formContainer{
        min-height: 100%;
        min-width: 100%;
        border-radius: 0;
        margin-right: 0;
    }
    .formTitle h4{
        font-size: 18px;
    }
    .formTitle span {
        font-size: 13px;
    }
    .btn{
        font-size: 15px;
    }
    .form input, .form textarea{
        padding: 8px;
    }
    .formTitle{

    }
}
